import s from './Post.module.css';

export const Post = (props) => {
  return (
    <div className={s.item}>
      <img src="https://cspromogame.ru//storage/upload_images/avatars/1299.jpg" />
      {props.message}
      <div>
        <span>Like {props.likesCount}</span>
      </div>
    </div>
  );
};
